a {
  text-decoration: none;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.producto {
  margin-top: 10rem;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 2rem;
  width: 100%;
}

.contenido {
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 1440px) {
  .contenido {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(2, 1fr);
  }
}

.imagen {
  width: 90%;
  background-image: url(../img/image-product-desktop.jpg);
  background-repeat: no-repeat;
}
@media (min-width: 1440px) {
  .imagen {
    width: 90%;
  }
}

.description {
  margin-top: 5rem;
  width: 100%;
  padding: 2rem;
}
.description__title {
  font-size: 3rem;
  letter-spacing: 4px;
  color: hsl(228deg, 12%, 48%);
  font-family: "Fraunces", serif;
}
@media (min-width: 1440px) {
  .description__title {
    font-size: 2rem;
  }
}
.description__heading {
  font-size: 4rem;
  width: 100%;
  margin: 0;
  color: hsl(212deg, 21%, 14%);
}
@media (min-width: 1440px) {
  .description__heading {
    width: 90%;
    font-size: 9rem;
  }
}
.description__text {
  width: 100%;
  color: hsl(228deg, 12%, 48%);
}
@media (min-width: 1440px) {
  .description__text {
    width: 90%;
  }
}

.precio {
  display: flex;
  justify-content: left;
  align-items: center;
  font-family: "Fraunces", serif;
}
.precio__norm {
  font-size: 5rem;
  font-weight: 700;
  color: hsl(158deg, 36%, 37%);
  margin-right: 2rem;
}
.precio__oferta {
  font-size: 2rem;
  color: hsl(228deg, 12%, 48%);
  text-decoration-line: line-through;
}

.boton {
  padding-top: 5rem;
}
.boton__btn {
  background-color: hsl(158deg, 36%, 37%);
  padding: 1.5rem 4rem;
  color: hsl(0deg, 0%, 100%);
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  font-weight: 700;
}
.boton__btn:hover {
  background-color: #2c5e4b;
}
.boton__btn::before {
  content: "";
  background-image: url(../img/icon-cart.svg);
  background-repeat: no-repeat;
  background-position: center;
  display: inline-block;
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
}
@media (min-width: 1440px) {
  .boton__btn {
    width: 85%;
  }
}